import React, { useState, useEffect, useRef } from "react";
import { useTheme } from "@material-ui/core/styles";
//import style
import useStyles from "../../../components/TableCommon/style/mainViewStyle";
//import lib
import {
  Typography,
  Card,
  Button,
  Divider,
  Grid,
  ButtonBase,
  Avatar,
  Tooltip,
  TableBody,
  Box,
  TableRow,
  TableCell
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import { useReactToPrint } from "react-to-print";
import {Modal} from 'antd'
import moment from "moment";

//import api

// import redux
import { customizeAction } from "../../../store/slice/customizeSlice";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";

//import constant
import * as HeadCells from "../../../assets/constant/tableHead";
import * as TableType from "../../../assets/constant/tableType";

////import project
import InvoiceFilter from "./InvoiceTool/InvoiceFilter";
import InvoiceTableRow from "./InvoiceTableRow/InvoiceTableRow";
//chung
import TableHeader from "../../../components/TableCommon/TableHeader/TableHeader";
import ToolBar from "../../../components/TableCommon/ToolBar/ToolBar";
import TableWrapper from "../../../components/TableCommon/TableWrapper/TableWrapper";

import JSONdata from "../../../assets/JsonData/invoice.json";
import orderApi from "../../../api/orderApi";

import useMediaQuery from "@material-ui/core/useMediaQuery";
import {BillMiniTableRow} from "../../../components/MiniTableRow/MiniTableRow"
import Pagination from "../../../components/TableCommon/TableWrapper/Pagination"
import { statusAction } from "../../../store/slice/statusSlice";
import {ThousandFormat, VNDFormat} from '../../../components/TextField/NumberFormatCustom'

const Invoice = () => {
  // fetch data here
  const theme = useTheme();
  const classes = useStyles(theme);
  const dispatch = useDispatch();
  const xsScreen = useMediaQuery(theme.breakpoints.down("xs")) ;


  const [orders, setOrders] = useState([]);
  const [pagingState, setPagingState] = useState({
    page: 0,
    limit: 10,
  });
  const [totalRows, setTotalRows] = useState(0);
  const [totalAmount, setTotalAmount] = useState(0);
  // api
  const info = useSelector((state) => state.info);
  const store_uuid = info.store.uuid;
  const branch_uuid = info.branch.uuid;


  /// search sort
  const initialQuery = {
    startDate: '',
    endDate: '',
    minDiscount: null,
    maxDiscount:null,
    minTotalAmount: null,
    maxTotalAmount: null,
    status: '',
    paymentMethod: '',
    orderBy: 'orders.created_at',
    sort: 'desc',
    searchKey: '',
  };

  const handleRemoveFilter = () => {
    setQuery(initialQuery)
  }
  const [query, setQuery] = useState(initialQuery)

  //// 2. Table
  //collapse
  const [openRow, setRowOpen] = React.useState(null);
  const handleOpenRow = (row) => {
    if (row !== openRow) {
      setRowOpen(row);
    } else {
      setRowOpen(null);
    }
  };

  // header sort
  const [order, setOrder] = React.useState("asc");
  const [orderBy, setOrderBy] = React.useState("id");
  const handleDeleteAll = () => {
    const deleteApi = async () => {
      console.log("delete api called")
      try {
        const res = await orderApi.deleteAll(store_uuid, branch_uuid);
        dispatch(statusAction.successfulStatus("Xa tt c ha n thnh cng"));
      } catch (err) {
        dispatch(statusAction.failedStatus("Xa tt c ha n tht bi"));
      }
    }
    Modal.confirm({
      content: "Bn chc chn mun xa tt c ha n, hnh ng ny khng th quay li",
      onOk() {
        deleteApi()
        onReload()
      },
    });
  }

  //3. ToolBar
  //3.2. filter
  // toolbar
  const componentRef = useRef();
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  });
  const [openFilter, setOpenFilter] = React.useState(false);
  const handleToggleFilter = () => {
    setOpenFilter(!openFilter);
  };
  const [reload, setReload] = useState(false);
  const onReload = () => {
    console.log("onReload called")
    setReload(!reload);
  };
  const handleRequestSort = (event, property) => {
    //// (gi order vs orderBy ln api) -> fetch li data  sort
    // const isAsc = orderBy === property && order === 'asc';
    // setOrder(isAsc ? 'desc' : 'asc');
    // setOrderBy(property);
  };

  useEffect(() => {
    setPagingState({ ...pagingState, page: 0 });
  }, [reload, store_uuid, branch_uuid, query]);
  useEffect(() => {
    const loadData = async () => {
      try {
        const response = await orderApi.getAllOfBranch(
          store_uuid,
          branch_uuid,
          {
            page: pagingState.page,
            limit: pagingState.limit,
            ...query,
          }
        );
        // setPagingState({ ...pagingState, total_rows: response.total_rows });
        setTotalRows(response.total_rows)
        setOrders(response.data);
        setTotalAmount(response.total_amount);
      } catch (error) {
        console.log(error);
      }
    };
    if (store_uuid && branch_uuid) {
      loadData();
    }
  }, [pagingState.page, pagingState.limit, branch_uuid, query, reload]);

  const tableRef = React.createRef();


  const getDataExport = async () => {
    try {
      const response = await orderApi.getAllOfBranch(
        store_uuid,
        branch_uuid,
        query,
      );
      return response.data;
    } catch (error) {
      dispatch(statusAction.failedStatus("Khng th ly d liu!"));
      console.log(error);
    }
  }

  return (
    <Card className={classes.root}>
      <Grid container direction="row" justifyContent="space-between">
        {/* 1. ADD POP UP */}
        <Grid direction="column" justifyContent="center" alignItems="center">
        <Typography className={classes.headerTitle} variant="h5">
          Ho n
        </Typography>
        {/* <Typography variant="body2" style={{paddingLeft: 25}}>
          <b style={{color:'#000'}}>{"S n: "}</b>
          <ThousandFormat value={totalRows}></ThousandFormat>
          {" - "}
          <b style={{color:'#000'}}>{"Tng: "}</b>
          <VNDFormat  value={totalAmount} />
        </Typography> */}
       
        </Grid>

        <Grid className={classes.btngroup1}>
          <ButtonBase
            sx={{ borderRadius: "16px" }}
            onClick={() => {
              dispatch(customizeAction.setSidebarOpen(false));
              dispatch(customizeAction.setItemMenuOpen(1));
            }}
            component={Link}
            to="/home/sales/cart"
          >
            <Avatar variant="rounded" className={classes.headerAvatar}>
              <Tooltip title="Thm ho n">
                <AddIcon stroke={1.5} size="1.3rem" />
              </Tooltip>
            </Avatar>
          </ButtonBase>
        </Grid>
      </Grid>

      <Divider />

      {/* 2. SEARCH - FILTER - EXPORT*/}
      {/* SAU NY SA LI TRUYN DATA SAU KHI FILTER, SORT, LC CT VO */}
      <ToolBar
        dataTable={orders}
        handleDeleteAll={handleDeleteAll}
        tableType={TableType.INVOICE}
        textSearch={"#, Khch, Ngi bn,...  "} /*handlePrint={handlePrint}*/
        handleToggleFilter={handleToggleFilter}
        handlePrint={handlePrint}
        getDataExport={getDataExport}
        orderByOptions={[
          { value: "orders.created_at", label: "Ngy bn" },
          { value: "total_amount", label: "Tng tin bn" },
        ]}
        orderBy={query.orderBy}
        setOrderBy={(value) => setQuery({ ...query, orderBy: value })}
        sort={query.sort}
        setSort={(value) => setQuery({ ...query, sort: value })}
        searchKey={query.searchKey}
        setSearchKey={(value) => setQuery({ ...query, searchKey: value })}
        handleRemoveFilter={handleRemoveFilter}
        columnsToKeep={[
          { dbName: "order_code", displayName: "M ho n" },
          { dbName: "customer_name", displayName: "Khch hng" },
          { dbName: "created_at", displayName: "Ngy bn" },
          { dbName: "total_amount", displayName: "Tng tin ho n" },
          { dbName: "paid_amount", displayName: "Tin khch  tr" },
          { dbName: "status", displayName: "Trng thi" },
          { dbName: "payment_method", displayName: "Phng thc thanh ton" },
          { dbName: "created_user_type", displayName: "Ti khon thc hin" },
          { dbName: "created_user_name", displayName: "Tn ngi thc hin" },
        ]}
      />
      <InvoiceFilter
        openFilter={openFilter}
        setQuery={setQuery}
        query={query}
        handleToggleFilter={handleToggleFilter}
        setOrders={setOrders}
      />

      {/* 3. TABLE */}
      {!xsScreen ? (
        <TableWrapper
          pagingState={{ ...pagingState, total_rows: totalRows }}
          setPagingState={setPagingState}
          list={orders}
          tableRef={tableRef}
        >
          <TableHeader
            classes={classes}
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
            headerData={HeadCells.InvoiceHeadCells}
          />
          <TableBody>
          <TableRow style={{backgroundColor:'#f5f5f5'}}>
              <TableCell style={{color:'#000', fontWeight:600}}>S n: <ThousandFormat value={totalRows}></ThousandFormat></TableCell>
              <TableCell/> <TableCell/> <TableCell/>
              <TableCell align="right"style={{color:'#000', fontWeight:600}}>Tng: <VNDFormat value={totalAmount} ></VNDFormat></TableCell>
              <TableCell/>
            </TableRow>
            {orders.map((row, index) => {
              return (
                <>
                {/* <TableRow style={{backgroundColor:theme.customization.primaryColor[50]}}> */}
                <InvoiceTableRow
                  key={row.uuid}
                  row={row}
                  openRow={openRow}
                  handleOpenRow={handleOpenRow}
                  onReload={onReload}
                  // hidenCollumn={[""]}
                />
                </>
              );
            })}
          </TableBody>
        </TableWrapper>
      ) : (
        <>
          <Box style={{ minHeight: "50vh" }}>
            {orders.map((row, index) => {
              return (
                // <InvoiceTableRow
                //   key={row.uuid}
                //   row={row}
                //   openRow={openRow}
                //   handleOpenRow={handleOpenRow}
                //   onReload={onReload}
                // />
                <BillMiniTableRow
                  key={row.uuid}
                  row={row}
                  openRow={openRow}
                  handleOpenRow={handleOpenRow}
                  onReload={onReload}
                  totalCost={row.total_amount}
                  id={row.order_code}
                  partnerName={row.customer_name}
                  date={row.paid_date}
                  typeBill={"Ho n"}
                />
              );
            })}
          </Box>
          <Pagination
            pagingState={{ ...pagingState, total_rows: totalRows }}
            setPagingState={setPagingState}
            list={orders}
          />
        </>
      )}
      <div style={{ display: "none" }}>
        <div ref={componentRef}>
          <ComponentToPrint orders={orders} classes={classes} query={query}  totalRows={totalRows}  totalAmount={totalAmount}/>
        </div>
      </div>
    </Card>
  );
};

export default Invoice;

const ComponentToPrint = ({ orders, classes,query ,totalRows,totalAmount }) => {
  const firstDate = orders.slice(-1)[0] ?orders.slice(-1)[0].creation_date.split(" ")[0].split('T')[0].split('-').reverse().join('/'):''
  return (
    <div style={{padding:10}}>
      <Typography style={{color:'#000'}}>Ngy lp:  {moment(new Date()).format("DD/MM/YYYY HH:mm")}</Typography>
      <Box style={{ margin: 10,flexGrow: 1,  textAlign: "center" ,color: "#000"}}>
        <Typography style={{  fontSize: 18, fontWeight: 600}} >
          {/* Thng k ho n */}
          THNG K HO N
        </Typography>
        <Typography  >
          {/* T {initialQuery.startDate} - Ngy {initialQuery.endDate} */}
          {`T ngy: ${query.startDate ? ` ${query.startDate.split('-').reverse().join('/')}` :firstDate} - n ngy: ${query.endDate? query.endDate.split('-').reverse().join('/') : moment(new Date()).format('DD/MM/YYYY')}`}
        </Typography>
        {query.searchKey ? <Typography  > {`Tm kim theo: ${query.searchKey}`} </Typography>:null}
        {query.status? <Typography  > {`Tnh trng n: ${query.status === "debt"?"N":"Tr "}`} </Typography>:null}
        {query.paymentMethod? <Typography  > {`Phng thc thanh ton: ${query.paymentMethod === "cash"?"Tin mt":"Th"}`} </Typography>:null}
        {query.minTotalAmount || query.maxTotalAmount ? <Typography  > {`Tng tin n t: ${query.minTotalAmount?query.minTotalAmount:0} n ${query.maxTotalAmount?query.maxTotalAmount:0}`} </Typography>:null}
        {query.minDiscount || query.maxDiscount ? <Typography  > {`n gim gi t: ${query.minDiscount?query.minDiscount:0} n ${query.maxDiscount?query.maxDiscount:0}`} </Typography>:null}

      </Box>
      <div>
      <TableWrapper  isReport={true} >
        <TableHeader
        color="#000"
          classes={classes}
          headerData={HeadCells.InvoiceHeadCells.filter(item => item.id !== "debt")}
        />
        <TableBody>
          {orders.map((row, index) => {
            return  <InvoiceTableRow colorText={"#000"} key={row.uuid} row={row} hidenCollumn={["debt"]}/>
            })}
            <TableRow style={{backgroundColor:'#f5f5f5'}}>
              <TableCell style={{color:'#000', fontWeight:600}}>S n: <ThousandFormat value={totalRows}></ThousandFormat></TableCell>
              <TableCell/> <TableCell/> <TableCell/>
              <TableCell align="right"style={{color:'#000', fontWeight:600}}>Tng: <VNDFormat value={totalAmount} ></VNDFormat></TableCell>
              <TableCell/>
            </TableRow>
        </TableBody>
        </TableWrapper>
      </div>
    </div>
  );
};
