import React from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { useState } from "react";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { Typography } from "@material-ui/core";
import * as _ from "lodash";
import {getColorBatch} from "../../utils"
import { Badge } from "antd";

export default function SelectBatch({ handleSubmit, handleClose, row, isInventoryCheck, isCart, isImport }) {
  const [selectedBatches, setSelectedBatches] = useState([]);

  return (
    <Dialog open={true}>
      {/* <DialogTitle
        style={{ fontSize: 16, paddingRight:30 }}
      >{`Chn l ca ${row.product_code} - ${row.name}`}</DialogTitle> */}
      <Typography style={{ fontSize: 16,fontWeight:600, marginTop:15, marginLeft:15, paddingRight:30 }}>{`Chn l ${row.product_code} - ${row.name}`}</Typography>

      <DialogContent>
        <Autocomplete
          options={isCart ? row.batches.filter(b => b?.quantity !== '0') : row.batches}
          getOptionLabel={(batch) =>
            `${batch?.batch_code} - ${
              batch?.expiry_date ? batch?.expiry_date.substring(0,10) : ""
            } - Tn kho: ${batch?.quantity}`
          }
          renderInput={(params) => (
            <TextField {...params} label="Tm m l" variant="standard" />
          )}
          onChange={(e, value) => {
            if (value) {
              setSelectedBatches(
                _.uniqBy(
                  [
                    ...selectedBatches,
                    ...[{ ...value, additional_quantity: 0, is_new: false }],
                  ],
                  "batch_code"
                )
              );
            }
          }}
        />
        <div style={{ marginTop: 20 }}>
          {selectedBatches.map((batch) => {
            return (
              <Badge.Ribbon offset={[10, 10]} text={getColorBatch(batch, row.notification_period).status} color={getColorBatch(batch, row.notification_period).color}>
                <div
                  key={batch?.batch_code}
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    gap: 10,
                    border: "2px solid #eee",
                    borderRadius: 5,
                    marginBottom: 10,
                    alignItems: "center",
                    width: 500,
                    justifyContent: "space-between",
                    paddingLeft: 10,
                    paddingRight: 50,
                    paddingTop: 5,
                    paddingBottom: 5,
                  }}
                >
                  <Typography variant="h5">{`${batch?.batch_code} - ${
                    batch?.expiry_date ? batch?.expiry_date?.substring(0, 10) : ""
                  } - Tn kho: ${batch?.quantity}`}</Typography>
                  <TextField
                    type="number"
                    style={{ width: 100 }}
                    value={batch.additional_quantity}
                    onChange={(e) => {
                      if (Number(e.target.value) < 0) return;
                      const newSelectedList = selectedBatches.map(
                        (selectedBatch) => {
                          if (selectedBatch.batch_code === batch.batch_code) {
                            return {
                              ...selectedBatch,
                              additional_quantity: Number(e.target.value),
                            };
                          } else {
                            return selectedBatch;
                          }
                        }
                      );
                      setSelectedBatches(newSelectedList);
                    }}
                  ></TextField>
                </div>
              </Badge.Ribbon>
            );
          })}
        </div>
      </DialogContent>
      <DialogActions>
        <Button color="secondary" size="small"variant="contained" onClick={handleClose}>
          Hy
        </Button>
        <Button
          color="primary"
          size="small"variant="contained"
          onClick={() => {
            if (!isInventoryCheck) {
              const nonZeroBatch = selectedBatches.filter(
                (batch) => batch.additional_quantity
              );
              handleSubmit(nonZeroBatch);
            } else {
              handleSubmit(selectedBatches);
            }
            handleClose();
          }}
        >
          Thm
        </Button>
      </DialogActions>
    </Dialog>
  );
}
